<template>
  <main>
    <div class="Orders" v-if="foodOrderData.length!=0">
      <section class="list" v-for="(item,index) in foodOrderData" :key="index">
        <div class="list-top clearfix">
          <div class="list-top-l fl">订单总额：
            <span class="red">￥{{item.realPrice|money}}</span>
          </div>
          <div class="list-top-r fr">
            <span class="red">{{item.payStatus|PaymentStatus}}</span>
            <span class="red" v-if="item.endTime!='00分00秒'">{{item.orderStatus|OrderStatus}}</span>
            <span class="red" v-if="item.endTime=='00分00秒'">已取消</span>
          </div>
        </div>
        <div class="list-bottom">
          <div class="list-bottom_above">
            <span>{{item.mergedFoodName}}</span>
          </div>
          <div class="list-bottom_middle clearfix">
            <div class="fl">
              <span>{{item.roomNum}}</span>
              <span>号房</span>
              <span class="red">({{item.payType|PaymentMethod}})</span>
            </div>
          </div>
          <div class="list-bottom_base">
            订餐时间：{{item.createTime}}
          </div>
          <p class="remain" v-if="item.payStatus==0&&item.endTime!='00分00秒'&&item.payType!=2&&item.orderStatus==0">支付剩余时间：
            <times :endTime.sync="item.endTime" :flag='timeshow'></times>
          </p>
        </div>
        <div class="orderNumber clearfix">
          <span class="fl">订单号：{{item.orderNum}}</span>
          <div class="fr">
            <router-link class="weui-btn weui-btn_mini weui-btn_warn" tag="a" :to="'/defrayal/'+item.id" v-if="item.payStatus==0&&item.endTime!='00分00秒'&&item.payType!=2&&item.orderStatus==0">
              支付
            </router-link>
            <a href="javascript:;" class="weui-btn weui-btn_mini weui-btn_primary" @click="cancelOrderfn(item.id)" v-if="item.payStatus==0&&item.endTime!='00分00秒'">取消</a>
          </div>
        </div>
      </section>
    </div>
    <LOGO v-if="foodOrderData.length!=0"></LOGO>
    <Nodata v-if="Nodata" msg='暂无数据'></Nodata>
  </main>
</template>

<script type='text/ecmascript-6'>
import LOGO from "@/components/Logo";
import { common, Personal } from "@/assets/js/request.js";
import { DateFormat } from "@/assets/js/filters.js";
import times from "@/components/times";
import Nodata from "@/components/Nodata.vue";
export default {
  data() {
    return {
      hotelId: sessionStorage.getItem("hotelId"),
      foodOrderData: [],
      page: 1,
      pages: 1,
      flag: true,
      timeshow: true,
      Nodata: false
    };
  },
  components: {
    LOGO,
    times,
    Nodata
  },
  methods: {
    getOrderfn() {
      if (this.flag) {
        this.flag = false;
        Personal.foodOrder({
          Suffix: this.hotelId + "/foodOrder",
          params: {
            hotelId: this.hotelId,
            page: this.page
          },
          fn: data => {
            if (data.records.length != 0) {
              this.foodOrderData = this.foodOrderData.concat(data.records);
              this.foodOrderData.forEach(function(item) {
                item.endTime =
                  Date.parse(new Date(item.createTime)) + 29 * 60 * 1000;
              }, this);
            } else {
              this.Nodata = true;
            }

            this.flag = true;
            this.pages = data.pages;
          }
        });
      }
    },
    cancelOrderfn(id) {
      common.cancelOrder({
        Suffix: this.hotelId + "/cancel/" + id,
        fn: data => {
          this.Toast({
            message: "取消成功",
            duration: 2000
          });
          var nowpage = this.page;
          this.roomsOrderData = [];
          for (var i = 0; i < nowpage; i++) {
            this.page = i + 1;
            this.getOrderfn();
          }
        }
      });
    }
  },
  mounted() {
    var _this = this;
    _this.getOrderfn();
    //获取滚动条当前的位置
    function getScrollTop() {
      var scrollTop = 0;
      if (document.documentElement && document.documentElement.scrollTop) {
        scrollTop = document.documentElement.scrollTop;
      } else if (document.body) {
        scrollTop = document.body.scrollTop;
      }
      return scrollTop;
    }

    //获取当前可是范围的高度
    function getClientHeight() {
      var clientHeight = 0;
      if (document.body.clientHeight && document.documentElement.clientHeight) {
        clientHeight = Math.min(
          document.body.clientHeight,
          document.documentElement.clientHeight
        );
      } else {
        clientHeight = Math.max(
          document.body.clientHeight,
          document.documentElement.clientHeight
        );
      }
      return clientHeight;
    }

    //获取文档完整的高度
    function getScrollHeight() {
      return Math.max(
        document.body.scrollHeight,
        document.documentElement.scrollHeight
      );
    }
    window.onscroll = function() {
      if (
        getScrollTop() + getClientHeight() > getScrollHeight() - 50 &&
        _this.flag
      ) {
        _this.page++;
        if (_this.page > _this.pages) {
          _this.Toast({
            message: "已加载所有数据",
            duration: 2000
          });
          _this.flag = false;
          return false;
        }
        _this.getOrderfn();
      }
    };
  }
};
</script>

<style scoped lang='less' rel='stylesheet/less'>
main {
  display: flex;
  flex-flow: column;
  min-height: 100vh;
  padding-bottom: 1.3rem;
}

.Orders {
  flex: 1; // background: #f4f4f4;
}
</style>



